@import url(https://fonts.googleapis.com/css?family=Roboto:400,700,900);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html{box-sizing:border-box}*,*:before,*:after{box-sizing:inherit}body{margin:0;padding:0}h1,h6{margin:2rem !important;text-align:center}h6{color:#00b96b}

.container{padding:0;display:flex;flex-wrap:wrap}.currencies{margin:0;padding:1rem;width:100px;text-align:center;background-color:#ffffff;border:none;border-bottom:1px solid #eeeeee;border-right:1px solid #eeeeee}.currencies:hover{background-color:#00b96b;font-weight:bold;color:#ffffff;cursor:pointer}p{font-size:1.5rem}.price{color:#00b96b}

